/* Global Styles */

body {
  place-items: center;
  background-color: #eaedf2;
  /* height: 100vh;
  margin: 3vmin; */
}

/* Calendar Container */

.calendar {
  --side-padding: 20px; 
  --border-radius: 34px; 
  --accent-br: 15px; 
  width: 400px;
  z-index: 2;
}

/* Calendar Options and Buttons */

.calendar__opts, .calendar__buttons {
  background-color: #fff;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 15px;
}

/* Calendar Options Section */

.calendar__opts {
  border-top-left-radius: var(--border-radius);
  border-top-right-radius: var(--border-radius);
  padding: 20px var(--side-padding);
}

/* Calendar Body */

.calendar__body {
  background-image: linear-gradient(to bottom, #f3f4f6, #fff);
}

/* Calendar Days Header */

.calendar__days {
  background-color: #fff;
  padding: 10px var(--side-padding) 10px;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
}  

.calendar__days > div {
  text-align: center;
  color: #c6e2f5;
  font-weight: bold;
}

/* Calendar Dates Grid */

.calendar__dates {
  padding: 10px var(--side-padding);
  display: grid;
  grid-template-columns: repeat(7, 1fr);
}

/* Calendar Individual Date Styles */

.calendar__date {
  --height: calc(400px / 6 - var(--side-padding));
  text-align: center;
  height: var(--height);
  line-height: var(--height);
  cursor: pointer;
  position: relative;
}

/* Calendar Date Hover Effect */

.calendar__date::before {
  content: "";
  position: absolute;
  background-color: rgba(255, 255, 255, 0);
  width: 100%;
  height: calc(var(--height) * 0.9);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: var(--accent-br);
  transition: background-color 0.3s ease;
}

.calendar__date:not(.calendar__date--selected):not(.calendar__date--grey):not(.other-month):hover::before {
  background-color: #ededed;
}

/* Other Month Styles */

.other-month {
  cursor: none;
}

.other-month:hover::before {
  background-color: transparent !important;
  transition: none !important; 
}

/* Selected Date Styles */

.calendar__date.selected {
  color: white;
}

.calendar__date.selected:hover{
  color: black;
} 

/* Span Styles */

span {
  position: relative;
  z-index: 1;
}

/* Button Styles */

button {
  border-radius: 20px;
  outline: none;
  border: 0;
  padding: 15px 20px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  cursor: pointer;
}

/* Custom Button Styles */

.btn{
  background-color:#284bbd;
  color: white;
  font-weight: bold;
  font-size: 0.7rem;
  cursor: pointer;
}

/* Button Hover Effect */

button:hover{
  background-color: #ededed;
  color: black;
}

/* Arrow Button Styles */

.arrow-left{
  background-color:#284bbd;
}

.arrow-right{
  background-color: #284bbd;
}

/* Flex Container Styles */

.flex-between-center{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Selected Date Hover Effect */

.calendar__date.selected::before{
  background-color: #284bbd;
}

